
import { HashRouter as Router, Route, Routes} from 'react-router-dom';
import Home from './Home';
import Categories from './Categories'
import Results from './Results';
import ReviewPage from './ReviewPage';
import Join from './Join';

import './index.css';

function App() {

  return (

    
      <Routes>
        <Route path='/' element={<Home/>} />
        <Route path='projet2_partie1_seg2525/#/Categories' element={<Categories/>} />
        <Route path='projet2_partie1_seg2525/#/ReviewPage' element={<ReviewPage/>} />
        <Route path='projet2_partie1_seg2525/#/Results/:category' element={<Results/>} />
        <Route path='projet2_partie1_seg2525/#/Join' element={<Join/>} />
      </Routes>
    
    
  );
}

export default App;
